.applying {
  margin-bottom: 133px;

  @include tablet {
    margin-bottom: 140px;
  }

  @include mobile {
    margin-bottom: 50px;
  }

  &__container {
    @include mobile {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
    }
  }

  &__top {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    margin-bottom: 88px;

    @include tablet {
      margin-bottom: 90px;
    }

    @include mobile {
      margin-bottom: 41px;
    }
  }

  &__link {
    margin: 0 70px 13px 0;
    padding: 9px 48px 5px 0;

    @include tablet {
      margin: 0 0 12px 0;
    }

    @include mobile {
      display: none;
    }

    &::after {
      top: 13px;
      width: 25px;
    }

    &--mobile {
      display: none;

      @include mobile {
        display: block;
      }
    }
  }

}

.portfolio-list {
  @include list-reset;
  display: flex;
  flex-wrap: wrap;
  // width: 100%;
  margin: 0 -25px 40px;
  // margin-bottom: 40px;

  @include tablet {
    margin: 0 -15px 30px;
    // margin-bottom: 30px;
  }

  @include mobile {
    flex-wrap: wrap;
    margin: 0;
  }


  &__item {
    position: relative;
    width: 50%;
    margin: 0 0 50px 0;
    padding: 0 25px;

    overflow: hidden;

    &:nth-of-type(2n) {
      margin-right: 0;
    }

    @include tablet {
      max-height: 409px;
      margin: 0 0 30px 0;
      padding: 0 15px;
    }

    @include mobile {
      width: 100%;
      margin: 0 0 21px;
      padding: 0;

      max-width: 100%;
      max-height: 100%;
    }
  }

}

.portfolio-item {
  width: 100%;
  // height: 100%;

  transition: opacity $transition-props;

  &:hover {
    .portfolio-item__text {
      opacity: 1;
    }

    img {
      opacity: 0;
    }
  }

  img {
    width: 100%;
    // height: 100%;
    // object-fit: cover;
    transition: opacity $transition-props;
  }

  &__size {
    margin: 0 0 6px;

    @include mobile {
      margin-bottom: 25px;
      padding-left: 12px;
      text-align: initial;
    }
  }

  &__title {
    font-size: 36px;
    line-height: 42px;
    font-weight: 400;

    margin: 0 0 13px;

    @media (max-width: 1070px) {
      font-size: 24px;
      line-height: 36px;

      margin: 0 0 8px;
    }
  }

  &__subtitle {
    margin: 0 0 4px;

    b {
      font-size: 16px;
      line-height: 19px;
      font-weight: 600;
      letter-spacing: 0.01em;

      margin-bottom: 3px;
      display: block;

      @media (max-width: 1024px) {
        display: none;
      }
    }
  }

  &__description {
    font-size: 16px;
    line-height: 26px;
    text-align: center;
    letter-spacing: 0.005em;

    color: #7c8592;

    max-width: 44%;
    margin: 0 0 32px;
  }


  &__text {
    position: absolute;
    top: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;
    padding-top: 34px;
    width: 94%;


    font-size: 16px;
    line-height: 26px;

    letter-spacing: 0.005em;

    text-align: center;
    z-index: 1;
    opacity: 0;

    @include tablet {
      width: 95%;
      padding-top: 0;
      padding-bottom: 10px;
      border: 1px solid rgba($color: #8ea0b9, $alpha: 0.5);
    }

    @include mobile {
      width: 100%;
    }

    address {
      position: relative;
      font-size: 16px;
      line-height: 26px;
      font-style: normal;

      margin-bottom: 26px;
      padding-left: 28px;

      letter-spacing: 0.005em;

      color: #7c8592;

      &::before {
        content: "";
        position: absolute;

        top: 1px;
        left: 5px;
        width: 12px;
        height: 17px;

        background-image: url("../img/icon-pin.svg");

        background-repeat: no-repeat;
        background-size: contain;

        @include mobile {
          top: 3px;
          left: -7px;
        }
      }
    }
  }
}
